<template>
  <div class="container">
    <div class="row" style="margin-top: 10rem;">
      <div class="col-sm-8 offset-sm-2">
        <div class="form-group">
          <input type="text" class="form-control" id="file-id" v-bind:placeholder="fileidplaceholder" v-model="fileId">
        </div>
        <a v-bind:href="downloadlink">Download link</a>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      fileidplaceholder: 'File ID here...',
      fileId: '',
    }
  },
  computed: {
    downloadlink () {
      var target = this.getTargetAPI()
      return target + '/improflash/getFile?FileID=' + this.fileId
    }
  },
  methods: {
    isEmpty: require('../../util/strings.js').isEmpty,
    trimStr: require('../../util/strings.js').trimStr,
    getTargetAPI: require('../../util/strings.js').getTargetAPI,
    submitFileId () {
      console.log('File ID: ' + this.fileId)
      var data = {
        FileID: this.fileId,
      }
      var target = this.getTargetAPI()
      console.log(target)
      this.$http.get(target + '/improflash/getFile', {params: data}, {emulateJSON: true}).then(response => {
        // success callback
        console.log(response)
      }, response => {
        // error callback
        console.log(response)
      })
    },
  },
}
</script>



