<template>
  <div class="container">
    <div class="row">
      <div class="col-sm-12 text-left">
        <h3>
          <span v-on:click="goback">
            <span class="glyphicon glyphicon-chevron-left float-left"></span>Go back
          </span>
        </h3>
      </div>
    </div>
    <div class="row">
      <div class="col-sm-12">
        <div class="form-group">
          <div class="input-group">
            <input type="text" id="keyword" class="form-control" placeholder="Search with keywords..." v-model="keyword">
            <span class="input-group-append" v-on:click="search">
              <i class="fa fa-search input-group-text"></i>
            </span>
          </div>
        </div>
      </div>
      <div class="col-sm-12">
        <div class="alert alert-warning" role="alert" v-bind:class="{ hidden: hideErr }">
          You should provide some keyword!
        </div>
      </div>
    </div>
    <div class="row">
      <div class="col-sm-12" v-if="hideErr && hasResults()">
        <div v-if="hasResults">
          <table class="table stats-table">
            <thead>
              <tr>
                <th>NO.</th>
                <th>File Name</th>
                <th>Status</th>
                <th>Date</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(result, index) in results" v-bind:key="result.title">
                <th scope="row">{{ index + 1 }}</th>
                <td>{{ result.title }}</td>
                <td><span class="label label-success">Accessable</span></td>
                <td><h5>{{ result.created }} <i class="fa fa-level-up"></i></h5></td>
              </tr>
            </tbody>
          </table>
        </div>
        <div class="col-sm-12" v-else>
          <div class="alert alert-warning" role="alert" v-bind:class="{ hidden: hideErr }">
            No items found!
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Search',
  data () {
    return {
      keyword: '',
      hideErr: true,
      results: [],
    }
  },
  methods: {
    goback () {
      this.$router.back()
    },
    search () {
      console.log('keyword: ' + this.keyword)
      this.hideErr = !this.isEmpty(this.keyword)
      if (this.hideErr) {
        // request search here
        this.results = require('./result.js').results
        console.log(this.results.length)
      }
    },
    isEmpty: require('../../util/strings.js').isEmpty,
    hasResults () {
      return this.results.length > 0
    }
  },
}
</script>



