<template>
  <div class="container">
    <div class="row">
      <div class="col-sm-12 text-left">
        <h3>
          <span v-on:click="goback">
            <span class="glyphicon glyphicon-chevron-left float-left"></span>Go back
          </span>
        </h3>
      </div>
    </div>
    <div class="row">
      <div class="col-sm-12">
        <h1>Edit Your Profile</h1>
      </div>
    </div>
    <div class="row">
      <div class="col-sm-4 offset-sm-4">
        <div class="form-group text-left">
          <label>User Name:</label>
          <input type="text" class="form-control" placeholder="ID NO." readonly v-model="profile.username" />
        </div>
        <!-- <div class="form-group text-left">
          <label>ID NO.:</label>
          <input type="text" class="form-control" placeholder="ID NO." v-model="profile.idno" />
        </div> -->
        <div class="form-group text-left">
          <label>Phone NO.:</label>
          <input type="text" class="form-control" placeholder="Phone NO." readonly v-model="profile.phone" />
        </div>
        <div class="form-group text-left">
          <label>E-mail:</label>
          <input type="text" class="form-control" placeholder="E-mail" readonly v-model="profile.email" />
        </div>
      </div>
    </div>
    <div class="row">
      <div class="col-sm-12">
        <a class="btn btn-primary disabled">Submit</a>
        <p>
          <small>Edit profile is not allowed now.</small>
        </p>
      </div>
    </div>
    <br />
    <br />
    <br />
    <div class="row">
      <div class="col-sm-12">
        <h1>Edit Your Password</h1>
      </div>
    </div>
    <div class="row">
      <div class="col-sm-4 offset-sm-4">
        <div class="form-group text-left">
          <label>Old password:</label>
          <input type="password" class="form-control" placeholder="ID NO." v-model="oldpassword" />
        </div>
        <div class="form-group text-left">
          <label>New password:</label>
          <input type="password" class="form-control" placeholder="Phone NO." v-model="password" />
        </div>
        <div class="form-group text-left">
          <label>Password confirm:</label>
          <input type="password" class="form-control" placeholder="E-mail" v-model="repassword" />
        </div>
      </div>
    </div>
    <div class="row">
      <div class="col-sm-12">
        <span class="btn btn-primary" v-on:click="changePassword">Submit</span>
      </div>
    </div>
    <br />
    <div class="row" v-bind:class="{ hidden: hideErr }">
      <div class="col-sm-4 offset-sm-4">
        <div class="alert alert-warning">
          <span>{{ errMsg }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Profile',
  data () {
    return {
      uid: localStorage.getItem('uid'),
      token: localStorage.getItem('token'),
      locale: localStorage.getItem('locale'),
      profile: {},
      errMsg: '',
      hideErr: true,
      oldpassword: '',
      password: '',
      repassword: '',
    }
  },
  created () {
    // request for exist profile here
    // this.profile = require('./profile.js').profile
    if (this.isEmpty(this.uid) || this.isEmpty(this.token)) {
      alert('Invalid login information!')
      this.logout()
    }
    var data = {
      uid: this.uid,
      token: this.token,
      locale: this.locale,
    }
    var target = this.getTargetAPI()
    this.$http.get(target + '/user/getInfo', {params: data}, {emulateJSON: true}).then(response => {
      // success callback
      console.log(response)
      this.profile = response.body
    }, response => {
      // error callback
      console.log(response)
      alert(response.body.detail)
      this.logout()
    })
  },
  methods: {
    isEmpty: require('../../util/strings.js').isEmpty,
    trimStr: require('../../util/strings.js').trimStr,
    getTargetAPI: require('../../util/strings.js').getTargetAPI,
    goback: function () {
      this.$router.back()
    },
    logout: function () {
      // do logout work here
      localStorage.setItem('token', null)
      localStorage.setItem('uid', null)
      this.$router.push('/logout')
    },
    changePassword () {
      this.hideErr = true
      if (!this.validateForm()) {
        return
      }
      var data = {
        uid: this.uid,
        old_password: this.oldpassword,
        new_password: this.password,
        new_password_again: this.repassword,
        locale: this.locale,
      }
      var target = this.getTargetAPI()
      this.$http.post(target + '/user/resetPassword', data, {emulateJSON: true}).then(response => {
        // success callback
        console.log(response)
        var msg = 'Reset password successfully!'
        alert(msg)
        this.$router.back()
      }, response => {
        // error callback
        console.log(response)
        this.errMsg = response.body.detail
        this.hideErr = false
      })
    },
    validateForm: function () {
      this.oldpassword = this.trimStr(this.oldpassword)
      if (this.isEmpty(this.oldpassword)) {
        this.errMsg = 'Please provide old password!'
        this.hideErr = false
        return false
      }

      // check password
      this.password = this.trimStr(this.password)
      this.repassword = this.trimStr(this.repassword)
      if (this.isEmpty(this.password) || this.password !== this.repassword) {
        this.errMsg = 'Please make sure your new password is confirmed!'
        this.hideErr = false
        return false
      }

      return true
    },
  },
}
</script>



