<template>
  <div class="container example-full">
    <div class="row">
      <div class="col-sm-12 text-left">
        <h3>
          <span v-on:click="goback">
            <span class="glyphicon glyphicon-chevron-left float-left"></span>Go back</span>
        </h3>
      </div>
    </div>
    <!-- title -->
    <div class="row">
      <div class="col-sm-12 text-center">
        <h2>File Upload</h2>
      </div>
    </div>
    <!-- // title -->
    <!-- alert message -->
    <div class="row" v-bind:class="{ hidden: hideErr }">
      <div class="col-sm-6 offset-sm-3">
        <div class="alert alert-warning">
          <span>{{ errMsg }}</span>
        </div>
      </div>
    </div>
    <!-- // alert message -->
    <div class="row">
      <div class="col-sm-12">
        <div class="form-group text-left">
          <label for="title">Your Title</label>
          <input type="text" id="title" class="form-control" placeholder="Your title..." v-model="title" v-bind:readonly="!editable">
          <small class="form-text text-muted">Your title for this upload.</small>
        </div>
        <div class="form-group text-left">
          <label for="author">Author</label>
          <input type="text" id="author" class="form-control" placeholder="Author name..." v-model="author" v-bind:readonly="!editable">
          <small class="form-text text-muted">It should be your user name.</small>
        </div>
        <div class="form-group text-left">
          <label>Country:</label>
          <basic-select :options="items"
            :selected-option="item"
            placeholder="select country"
            @select="onSelect">
          </basic-select>
        </div>
        <div class="form-group text-left">
          <label for="description">Your Description</label>
          <textarea id="description" class="form-control" placeholder="Your description..." rows="5" v-model="description" v-bind:readonly="!editable"></textarea>
          <small class="form-text text-muted">Your description for this upload.</small>
        </div>
      </div>
    </div>
    <hr />
    <div class="table-responsive">
      <table class="table table-hover">
        <thead>
          <tr>
            <th>#</th>
            <th>Thumb</th>
            <th>Name</th>
            <th>Size</th>
            <th>Speed</th>
            <th>Status</th>
            <th>Action</th>
          </tr>
        </thead>
        <tbody>
          <tr v-if="!files.length">
            <td colspan="7">
              <div class="text-center p-5">
                <h4>Please select file</h4>
                <label v-bind:for="name" class="btn btn-primary">Select Files</label>
              </div>
            </td>
          </tr>
          <tr v-for="(file, index) in files" :key="file.id">
            <td>{{index}}</td>
            <td>
              <img v-if="file.thumb" :src="file.thumb" width="40" height="auto" />
              <span v-else>No Image</span>
            </td>
            <td>
              <div class="filename">
                {{file.name}}
              </div>
            </td>
            <td>{{file.size | formatSize}}</td>
            <td>{{file.speed | formatSize}}</td>
            <!-- status column -->
            <td v-if="file.error">{{file.error}}</td>
            <td v-else-if="file.success">success</td>
            <td v-else-if="file.active">active</td>
            <td v-else></td>
            <!-- // status column -->
            <td>
              <div class="btn-group">
                <a class="dropdown-item" href="javascript:void(0)" @click.prevent="$refs.upload.remove(file)" v-if="editable">Remove</a>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <file-upload
      class="btn btn-primary"
      ref="upload"
      v-model="files"
      v-bind:post-action=uploadapi
      accept="image/*"
      :multiple="true"
      :thread="3"
      v-bind:class="{ disabled: !editable }"
      @input-file="inputFile"
      @input-filter="inputFilter"
    >
    Add File
    </file-upload>
    <button class="btn btn-success" v-show="!$refs.upload || !$refs.upload.active" @click="startUpload" type="button">Upload Start</button>
    <button class="btn btn-danger" v-show="$refs.upload && $refs.upload.active" @click.prevent="$refs.upload.active = false" type="button">Upload Stop</button>
  </div>
</template>

<script>
import VueUploadComponent from 'vue-upload-component'
import { BasicSelect } from 'vue-search-select'
export default {
  name: 'UploadFile',
  components: {
    FileUpload: VueUploadComponent,
    BasicSelect,
  },
  data () {
    return {
      uploadapi: '',
      files: [],
      name: 'file',
      title: '',
      description: '',
      editable: true,
      jobid: '',
      errMsg: '',
      hideErr: true,
      nations: require('../../util/nations.js').nations,
      items: [],
      item: {
        value: '',
        text: '',
      },
      country: '',
      author: '',
      uid: localStorage.getItem('uid'),
      token: localStorage.getItem('token'),
      locale: localStorage.getItem('locale'),
    }
  },
  created () {
    console.log('You are in file upload page...')
    this.uploadapi = this.getTargetAPI() + '/uploader/upload'
    // initialize search select
    this.refreshItems()
    // create jobid here
  },
  filters: {
    formatSize: function (value) {
      var formated = value / (1 << 10)
      formated = formated.toFixed(2)
      formated = formated + 'KB'
      return formated
    },
  },
  methods: {
    isEmpty: require('../../util/strings.js').isEmpty,
    trimStr: require('../../util/strings.js').trimStr,
    getTargetAPI: require('../../util/strings.js').getTargetAPI,
    /**
     * Pretreatment
     * @param  Object|undefined   newFile   
     * @param  Object|undefined   oldFile   
     * @param  Function           prevent   
     * @return undefined
     */
    inputFilter: function (newFile, oldFile, prevent) {
      if (newFile && !oldFile) {
        // Filter system files or hide files
        //  
        if (/(\/|^)(Thumbs\.db|desktop\.ini|\..+)$/.test(newFile.name)) {
          return prevent()
        }

        // Filter php html js file
        //  php html js 
        if (/\.(php5?|html?|jsx?)$/i.test(newFile.name)) {
          return prevent()
        }

        // 
        if (!/\.(jpeg|jpe|jpg|gif|png|webp)$/i.test(newFile.name)) {
          return prevent()
        }

        //  blob  
        newFile.blob = ''
        let URL = window.URL || window.webkitURL
        if (URL && URL.createObjectURL) {
          newFile.blob = URL.createObjectURL(newFile.file)
        }

        // Thumbnails
        // 
        newFile.thumb = ''
        if (newFile.blob && newFile.type.substr(0, 6) === 'image/') {
          newFile.thumb = newFile.blob
        }
      }
    },
    /**
     * Has changed
     * @param  Object|undefined   newFile   
     * @param  Object|undefined   oldFile   
     * @return undefined
     */
    inputFile: function (newFile, oldFile) {
      if (newFile && oldFile && !newFile.active && oldFile.active) {
        // 
        console.log('response', newFile.response)
        if (newFile.xhr) {
          //  
          console.log('status', newFile.xhr.status)
        }
      }
    },
    goback: function () {
      this.$router.back()
    },
    startUpload () {
      if (!this.validateForm()) {
        return
      }
      if (confirm('If you start to upload, you can\'t change the form.')) {
        console.log('start...')
        this.editable = false
        // start uploading files
        this.$refs.upload.active = true
      }
    },
    validateForm () {
      this.hideErr = true
      this.title = this.trimStr(this.title)
      if (this.isEmpty(this.title)) {
        this.errMsg = 'Please provide title!'
        this.hideErr = false
        return false
      }

      this.author = this.trimStr(this.author)
      if (this.isEmpty(this.author)) {
        this.errMsg = 'Please provide author!'
        this.hideErr = false
        return false
      }

      this.country = this.trimStr(this.country)
      if (this.isEmpty(this.country)) {
        this.errMsg = 'Please provide country!'
        this.hideErr = false
        return false
      }

      this.description = this.trimStr(this.description)
      if (this.isEmpty(this.description)) {
        this.errMsg = 'Please provide description!'
        this.hideErr = false
        return false
      }

      if (this.files.length < 1) {
        this.errMsg = 'Please select file!'
        this.hideErr = false
        return false
      }
      return true
    },
    refreshItems () {
      console.log('refresh items...')
      this.items = []
      for (var i = 0; i < this.nations.length; i++) {
        var item = {}
        item.text = this.locale === 'en' ? this.nations[i].English : this.nations[i].China
        item.value = this.nations[i].ISO3
        this.items.push(item)
      }
    },
    onSelect (item) {
      if (this.editable) {
        this.item = item
        this.country = this.item.value
        console.log('selected: ' + this.country)
      }
    },
    selectOption () {
      // select option from parent component
      this.item = this.items[0]
    },
  }
}
</script>



