<template>
  <div class="background" v-bind:style="{ backgroundImage: 'url(' + require('../../assets/signinbg.png') + ')', backgroundSize: 'cover', overflow: 'hidden' }">
    <div class="row hidden">
      <h4>{{ $route.params.msg }}</h4>
    </div>
    <div class="row">
      <div class="col-sm-4 offset-sm-4">
        <div id="sign-in-form" class="well">
          <p id="title">Login</p>
          <div class="form-group text-left">
            <input type="text" class="form-control" id="username" v-bind:placeholder="usernameholder" v-model="username" />
          </div>
          <div class="form-group text-left">
            <input type="password" class="form-control" id="password" v-model="password" v-bind:placeholder="passwordholder" />
          </div>
          <div class="form-group">
            <button id="sign-in" class="btn btn-primary col text-uppercase" v-on:click="login" v-bind:class="{ disabled: !validateForm() }">Login</button>
          </div>
          <div class="form-group text-center h3">
              Not a member? <a href="javascript:void(0)" style="border-bottom: 1px solid" v-on:click="register">Sign up now</a>
          </div>
        </div>
        <div class="alert alert-warning" v-bind:class="{ hidden: hideErr }">
          <span>{{ errMsg }}</span>
        </div>
      </div>
    </div>
    <br/>
    <!-- <div class="row">
      <div class="col-sm-4 offset-sm-4">
        <button class="btn btn-warning" v-on:click="forget">Forget password</button>
      </div>
    </div> -->
    <div class="locale">
      <select v-model="locale" v-on:change="changeLocale" v-bind:style="{ background: 'url(' + require('../../assets/arrow-down.png') + ') no-repeat scroll right center transparent', paddingRight: '2rem' }">
        <option value="zh"></option>
        <option value="en">English</option>
      </select>
    </div>
  </div>
</template>

<script>
export default {
  name: 'LoginPage',
  data () {
    return {
      windowheight: window.innerHeight,
      usernameholder: this.$t('login.username'),
      passwordholder: this.$t('login.password'),
      username: '',
      password: '',
      locale: localStorage.getItem('locale'),
      hideErr: true,
      errMsg: '',
    }
  },
  created () {
    this.locale = localStorage.getItem('locale')
    if (this.locale == null) {
      this.locale = 'en'
      localStorage.setItem('locale', this.locale)
    }
    this.$i18n.locale = this.locale
  },
  mounted () {
    this.resizePage()
    $(window).resize(() => {
      this.resizePage()
    })
  },
  methods: {
    isEmpty: require('../../util/strings.js').isEmpty,
    trimStr: require('../../util/strings.js').trimStr,
    getTargetAPI: require('../../util/strings.js').getTargetAPI,
    resizePage () {
      $('body').css('height', window.innerHeight + 'px')
      $('body').css('width', window.innerWidth + 'px')
      $('.background').css('height', window.innerHeight + 'px')
      $('.background').css('width', window.innerWidth + 'px')
      $('#sign-in-form').css('margin-top', window.innerHeight * 0.25 + 'px')
    },
    changeLocale: function () {
      console.log(this.locale)
      localStorage.setItem('locale', this.locale)
      this.$i18n.locale = this.locale
      this.usernameholder = this.$t('login.username')
      // this.passwordholder = this.$t('login.password')
    },
    login: function () {
      this.hideErr = true
      var data = {
        username: this.username,
        password: this.password,
        locale: this.locale,
      }
      var target = this.getTargetAPI()
      this.$http.post(target + '/user/login', data, {emulateJSON: true}).then(response => {
        // success callback
        console.log(response)
        localStorage.setItem('token', response.body.token)
        localStorage.setItem('uid', response.body.uid)
        this.$router.push('/main')
      }, response => {
        // error callback
        console.log(response)
        localStorage.setItem('token', null)
        localStorage.setItem('uid', null)
        this.errMsg = response.body.detail
        this.hideErr = false
      })
    },
    register: function () {
      this.$router.push('/register')
    },
    forget: function () {
      console.log('go to forget password page...')
      // this.$router.push('/forget')
    },
    validateForm: function () {
      // check user name
      this.username = this.trimStr(this.username)
      // console.log(this.username + '|')
      if (this.isEmpty(this.username)) {
        return false
      }

      // check password
      this.password = this.trimStr(this.password)
      // console.log(this.password + '|')
      // console.log(this.repassword + '|')
      if (this.isEmpty(this.password)) {
        return false
      }

      return true
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
input {
  border: 0px;
  background-color: #ECF3FC;
}
#title {
  font-weight: 700;
  font-size: 4rem;
  margin-top: 1rem;
  margin-bottom: 2.6rem;
}
#sign-in {
  font-size: 1.5rem;
  font-weight: 700;
  margin-top: 3rem;
  margin-bottom: 1rem;
  height: 3.2rem;
}
#sign-in-form {
  border-radius: 10px;
  background-color: #ffffff;
  padding: 2.4rem;
}
.locale {
  position: fixed;
  right: 2%;
  top: 2%;
}
.locale>select {
  color: #ffffff;
  font-size: 2rem;
  background-color: transparent;
  outline: none;
  border: 0px;
  border-bottom: 2px solid #ffffff;
  border-radius: 0px;
  -webkit-border-radius: 0px;
  -webkit-appearance: none;
}
</style>



