<template>
  <div class="container">
    <div class="row">
      <div class="col-12">
        <h1>Main Page</h1>
      </div>
    </div>
    <div class="row">
      <div class="col-12">
        <ul class="nav justify-content-center">
          <li class="nav-item">
            <a class="nav-link disabled" href="javascript:void(0)">Main</a>
          </li>
          <li class="nav-item">
            <a class="nav-link disabled" href="javascript:void(0)">Link</a>
          </li>
          <li class="nav-item">
            <a class="nav-link active" href="javascript:void(0)" v-on:click="logout(false)">Logout</a>
          </li>
        </ul>
      </div>
    </div>
    <div class="row">
      <div class="col-sm-3">
        <feature-card
          card-id="file-list-card"
          v-bind:card-func=fileListCardFunc
          card-title="File List"
          card-desc="Your file list"
          v-bind:card-icon="fileListCardIcon"
        ></feature-card>
      </div>
      <div class="col-sm-3">
        <feature-card
          card-id="account-card"
          v-bind:card-func=accountCardFunc
          card-title="Account"
          card-desc="Your account"
          v-bind:card-icon="accountCardIcon"
        ></feature-card>
      </div>
      <div class="col-sm-3">
        <feature-card
          card-id="upload-card"
          v-bind:card-func=uploadCardFunc
          card-title="Upload files"
          card-desc="Upload Interlectual property."
          v-bind:card-icon="uploadCardIcon"
        ></feature-card>
      </div>
      <div class="col-sm-3">
        <feature-card
          card-id="search-card"
          v-bind:card-func=searchCardFunc
          card-title="Search files"
          card-desc="Search Interlectual property."
          v-bind:card-icon="searchCardIcon"
        ></feature-card>
      </div>
      <div class="col-sm-3">
        <feature-card
          card-id="profile-card"
          v-bind:card-func=profileCardFunc
          card-title="Profile"
          card-desc="Edit your profile or password."
          v-bind:card-icon="profileCardIcon"
        ></feature-card>
      </div>
    </div>
  </div>
</template>

<script>
import FeatureCard from './FeatureCard.vue'
export default {
  name: 'MainPage',
  components: {
    FeatureCard,
  },
  data () {
    return {
      fileListCardIcon: require('@/assets/folder.png'),
      accountCardIcon: require('@/assets/wallet.png'),
      uploadCardIcon: require('@/assets/upload.png'),
      searchCardIcon: require('@/assets/search.png'),
      profileCardIcon: require('@/assets/avatar.png'),
      uid: localStorage.getItem('uid'),
      token: localStorage.getItem('token'),
      locale: localStorage.getItem('locale'),
    }
  },
  created () {
    // validate token here
    var data = {
      token: this.token,
      locale: this.locale,
    }
    var target = this.getTargetAPI()
    this.$http.get(target + '/user/validateToken', {params: data}, {emulateJSON: true}).then(response => {
      // success callback
      console.log(response)
    }, response => {
      // error callback
      console.log(response)
      alert(response.body.detail)
      this.logout(true)
    })
    if (this.token === null || this.token === 'null' || this.token.length <= 4) {
      this.logout(true)
      return
    }
    // get user information and change the profile card
    console.log(localStorage.getItem('uid'))
  },
  methods: {
    getTargetAPI: require('../../util/strings.js').getTargetAPI,
    logout: function (noAsk) {
      // do logout work here
      if (noAsk || confirm('Are you sure to logout?')) {
        localStorage.setItem('token', null)
        localStorage.setItem('uid', null)
        this.$router.push('/logout')
      }
    },
    fileListCardFunc () {
      this.$router.push({path: '/file/list'})
    },
    accountCardFunc () {
      this.$router.push({path: '/account'})
    },
    uploadCardFunc () {
      this.$router.push({path: '/uploadfile'})
    },
    searchCardFunc () {
      this.$router.push({path: '/search'})
    },
    profileCardFunc () {
      this.$router.push({path: '/profile'})
    },
  },
}
</script>



