<template>
  <div class="background" v-bind:style="{ backgroundImage: 'url(' + require('../../assets/signinbg.png') + ')', backgroundSize: 'cover', overflow: 'hidden' }">
    <div class="row">
      <div class="col-sm-4 offset-sm-4">
        <div id="sign-up-form" class="well">
          <div class="form-group text-left" style="margin-top: 2rem;">
            <input type="text" class="form-control" id="username" v-bind:placeholder="usernameholder" v-model="username" />
          </div>
          <div class="form-group text-left">
            <input type="password" class="form-control" id="password" v-model="password" v-bind:placeholder="passwordholder" v-on:input="changePassword" />
          </div>
          <div class="form-group text-left">
            <input type="password" class="form-control" id="repassword" v-model="repassword" v-bind:placeholder="repasswordholder" v-on:input="changePassword" />
          </div>
          <div class="form-group text-left">
            <input type="text" class="form-control" v-bind:placeholder="idnoholder" v-model="idno" />
          </div>
          <div class="form-group text-left">
            <input type="text" class="form-control" v-bind:placeholder="phoneholder" v-model="phone" />
          </div>
          <div class="form-group text-left">
            <input type="text" class="form-control" v-bind:placeholder="emailholder" v-model="email" />
          </div>
          <div class="form-group text-left">
            <input type="text" class="form-control" v-bind:placeholder="walletholder" v-model="wallet" />
          </div>
          <div class="form-group text-left">
            <basic-select
              id="country-picker"
              :options="items"
              :selected-option="item"
              v-bind:placeholder="countryholder"
              @select="onSelect">
            </basic-select>
          </div>
          <div id="gender-picker" class="well form-group text-left">
            Gender
            <span class="float-right">
              <span class="choice-item" style="margin-right: 2.3rem;">
                <input type="radio" value="M" class="choice-radio" v-model="gender">Male
              </span>
              <span class="choice-item">
                <input type="radio" value="F" class="choice-radio" v-model="gender">Female
              </span>
            </span>
          </div>
          <button class="btn btn-primary col text-uppercase" style="margin-top: 1.5rem; height: 2.7rem; font-size: 1.5rem; font-weight: 700;" v-on:click="register" v-bind:class="{ disabled: !validateForm() }">Sign up</button>
        </div>
        <div class="alert alert-warning" v-bind:class="{ hidden: hideErr }">
          <span>{{ errMsg }}</span>
        </div>
      </div>
    </div>
    <br/>
    <!-- <div class="row">
      <div class="col-sm-4 offset-sm-4">
        <button class="btn btn-success" v-on:click="back">{{$t('register.back')}}</button>
      </div>
    </div> -->
    <br/>
    <div class="row" v-show="showTip">
      <div class="col-sm-4 offset-sm-4 text-center">
        <p class="alert alert-danger">{{$t('register.tip')}}</p>
      </div>
    </div>
    <select class="locale" v-model="locale" v-on:change="changeLocale" v-bind:style="{ background: 'url(' + require('../../assets/arrow-down.png') + ') no-repeat scroll right center transparent', paddingRight: '2rem' }">
      <option value="zh"></option>
      <option value="en">English</option>
    </select>
  </div>
</template>

<script>
import { BasicSelect } from 'vue-search-select'
export default {
  name: 'RegisterPage',
  components: {
    BasicSelect,
  },
  data () {
    return {
      usernameholder: 'User name',
      passwordholder: 'Password',
      repasswordholder: 'Confirm your password',
      idnoholder: 'Your ID NO. or passport number',
      phoneholder: 'Phone NO.',
      emailholder: 'Your email address',
      walletholder: 'Your existing wallet(Optional)',
      countryholder: 'Your country',
      genderholder: 'Your gender',
      username: '',
      password: '',
      repassword: '',
      idno: '',
      phone: '',
      email: '',
      wallet: '',
      country: '',
      gender: '',
      showTip: false,
      locale: localStorage.getItem('locale'),
      nations: require('../../util/nations.js').nations,
      items: [],
      item: {
        value: '',
        text: '',
      },
      hideErr: true,
      errMsg: '',
    }
  },
  created () {
    this.locale = localStorage.getItem('locale')
    if (this.locale == null) {
      this.locale = 'en'
      localStorage.setItem('locale', this.locale)
    }
    this.$i18n.locale = this.locale

    // initialize search select
    this.refreshItems()
  },
  watch: {
    locale: function () {
      this.refreshItems()
    },
  },
  mounted () {
    this.resizePage()
    $(window).resize(() => {
      this.resizePage()
    })
  },
  methods: {
    isEmpty: require('../../util/strings.js').isEmpty,
    trimStr: require('../../util/strings.js').trimStr,
    getTargetAPI: require('../../util/strings.js').getTargetAPI,
    resizePage () {
      $('body').css('height', window.innerHeight + 'px')
      $('body').css('width', window.innerWidth + 'px')
      $('.background').css('height', window.innerHeight + 'px')
      $('.background').css('width', window.innerWidth + 'px')
      $('#sign-up-form').css('margin-top', window.innerHeight * 0.15 + 'px')
    },
    refreshItems () {
      console.log('refresh items...')
      this.items = []
      for (var i = 0; i < this.nations.length; i++) {
        var item = {}
        item.text = this.locale === 'en' ? this.nations[i].English : this.nations[i].China
        item.value = this.nations[i].ISO3
        this.items.push(item)
      }
    },
    register: function () {
      this.hideErr = true
      if (!this.validateForm()) {
        return
      }
      var data = {
        username: this.username,
        password: this.password,
        id_number: this.idno,
        phone: this.phone,
        email: this.email,
        wallet: this.wallet,
        country: this.country,
        gender: this.gender,
        locale: this.locale,
      }
      // OR use FormData here without emulateJSON
      // var data = new FormData()
      // data.append('username', this.username)
      // var target = 'http://localhost:8800'
      var target = this.getTargetAPI()
      this.$http.post(target + '/user/create', data, {emulateJSON: true}).then(response => {
        // success callback
        console.log(response)
        var msg = 'Register successfully!'
        alert(msg)
        this.$router.push({path: '/login/' + msg})
      }, response => {
        // error callback
        console.log(response)
        this.errMsg = response.body.detail
        this.hideErr = false
      })
    },
    onSelect (item) {
      this.item = item
      this.country = this.item.value
      console.log('selected: ' + this.country)
    },
    reset () {
      this.item = {}
    },
    selectOption () {
      // select option from parent component
      this.item = this.items[0]
    },
    changeLocale: function () {
      console.log(this.locale)
      localStorage.setItem('locale', this.locale)
      this.$i18n.locale = this.locale
    },
    back: function () {
      this.$router.push('/')
    },
    changePassword: function () {
      if (this.password !== this.repassword) {
        this.showTip = true
      } else {
        this.showTip = false
      }
    },
    validateForm: function () {
      // check user name
      this.username = this.trimStr(this.username)
      // console.log(this.username + '|')
      if (this.isEmpty(this.username)) {
        return false
      }

      // check password
      this.password = this.trimStr(this.password)
      this.repassword = this.trimStr(this.repassword)
      // console.log(this.password + '|')
      // console.log(this.repassword + '|')
      if (this.isEmpty(this.password) || this.password !== this.repassword) {
        return false
      }

      // check id no.
      this.idno = this.trimStr(this.idno)
      if (this.isEmpty(this.idno)) {
        return false
      }

      // check phone.
      this.phone = this.trimStr(this.phone)
      if (this.isEmpty(this.phone)) {
        return false
      }

      // check email.
      this.email = this.trimStr(this.email)
      if (this.isEmpty(this.email)) {
        return false
      }

      // check country.
      this.country = this.trimStr(this.country)
      if (this.isEmpty(this.country)) {
        return false
      }

      // check gender.
      this.gender = this.trimStr(this.gender)
      if (this.isEmpty(this.gender)) {
        return false
      }

      return true
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
input {
  border: 0px;
  background-color: #ECF3FC;
}
#sign-up-form {
  border-radius: 10px;
  background-color: #ffffff;
  padding: 2.4rem;
}
#country-picker {
  border: 0px;
  background-color: #ECF3FC;
}
#gender-picker {
  border: 0px;
  background-color: #ECF3FC;
  padding: 0.6rem;
}
.choice-radio {
  margin-right: 0.8rem;
}
.locale {
  font-size: 2rem;
  position: fixed;
  right: 2%;
  top: 2%;
  color: #ffffff;
  font-size: 2rem;
  background-color: transparent;
  outline: none;
  border: 0px;
  border-bottom: 2px solid #ffffff;
  border-radius: 0px;
  -webkit-border-radius: 0px;
  -webkit-appearance: none;
}
</style>



