<template>
  <div class="container">
    <div class="row">
      <div class="col-sm-12 text-left">
        <h3>
          <span v-on:click="goback">
            <span class="glyphicon glyphicon-chevron-left float-left"></span>Go back
          </span>
        </h3>
      </div>
    </div>
    <div class="row">
      <div class="col-12">
        <h1>File List</h1>
      </div>
    </div>
    <div class="row">
      <table class="table stats-table">
        <thead>
          <tr>
            <th>NO.</th>
            <th>File Name</th>
            <th>Status</th>
            <th>Date</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(archive, index) in archives" v-bind:key="archive.title" v-on:click="previewArchive(index)">
            <th scope="row">{{ index + 1 }}</th>
            <td>{{ archive.title }}</td>
            <td><span class="label label-success">Accessable</span></td>
            <td><h5>{{ archive.created }} <i class="fa fa-level-up"></i></h5></td>
          </tr>
          <tr v-if="!archives.length">
            <td colspan="4">
              <div class="text-center p-5">
                <h4>You haven't uploaded any files yet.</h4>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
export default {
  name: 'FileList',
  data () {
    return {
      // archives: require('./files.js').archives,
      archives: [],
    }
  },
  created () {
    // request for file list here
  },
  methods: {
    goback: function () {
      this.$router.back()
    },
    previewArchive (idx) {
      var file = this.archives[idx]
      this.$router.push({path: '/file/preview/' + file.fileId})
    },
  },
}
</script>



