<template>
  <div class="card" v-bind:id="cardId" v-bind:title="cardDesc" v-on:click="cardFunc">
    <img class="card-img-top" v-bind:src="cardIcon" alt="Icon" />
    <div class="card-body">
      <h5 class="card-title">{{ cardTitle }}</h5>
      <p class="card-text" role="tooltip" v-bind:class="{ hidden: hideDesc }">{{ cardDesc }}</p>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    cardId: String,
    cardFunc: Function,
    cardTitle: String,
    cardDesc: String,
    cardIcon: String,
  },
  data () {
    return {
      hideDesc: true,
    }
  },
  created () {
  },
  mounted () {
    $('#' + this.cardId).hover((e) => {
      this.hideDesc = false
    }, (e) => {
      this.hideDesc = true
    })
  },
}
</script>



